main {
	margin: 0 auto;
	max-width: 90%;
	padding: 20px 30px;
}

:root {
	--primary: #4e6c50;
	--secondary: #4e6c50;
	--tertiary: #aa8b56;
	--light: #f0ebce;
	--dark: #395144;
	--darker: #202d26;
	--error: rgb(228, 46, 1);
	--spacing-one: calc(1rem * 0.5);
	--spacing-two: calc(1rem);
	--spacing-five: calc(2.5rem);
}

* {
	box-sizing: border-box;
}

html,
body {
	min-height: 100%;
}

body {
	margin: 0;
	padding: 0;
	font-weight: 600;
	line-height: 1.5;
	font-size: 18px;
	letter-spacing: 0.1em;
	background: var(--dark);
	color: var(--light);
	font-family: 'Poiret One', cursive;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Roboto', sans-serif;
	font-weight: 500;
	margin: 5px 0;
	line-height: 1.25;
}

h1 {
	font-size: 2.25rem;
}

h2 {
	font-size: 2rem;
}

h3 {
	font-size: 1.75rem;
}

h4 {
	font-size: 1.5rem;
}

h5 {
	font-size: 1.25rem;
}

h6 {
	font-size: 1rem;
}

p {
	font-size: 1.1rem;
	margin-top: 0;
}

a {
	color: var(--tertiary);
	text-decoration: none;
	font-weight: bold;
}

a:hover {
	text-decoration: underline;
}

